import { useState, useEffect } from "react";
import prisma from "@/prisma/db";
import WorkshopCard from "@/components/WorkshopCard";
import FilterBar from "@/components/FilterBar";
import "../css/components/page.css";

export default function Home() {
  const [workshops, setWorkshops] = useState([]);
  const [loading, setLoading] = useState(true);

  const fetchWorkshops = async (searchTerm = "") => {
    setLoading(true);
    const res = await fetch(`/api/search-workshops?searchTerm=${searchTerm}`);
    const data = await res.json();
    setWorkshops(data.workshops);
    setLoading(false);
  };

  useEffect(() => {
    fetchWorkshops(); // Lade Workshops beim Start
  }, []);

  return (
    <>
      <section className="hero-section">
        <div className="filter-container">
          <FilterBar onSearch={fetchWorkshops} />
        </div>
      </section>
      <main className="container">
        <h1 className="text-center">Neuste Workshops</h1>
        {loading ? (
          <p>Lade Workshops...</p>
        ) : (
          <div className="workshops-grid">
            {workshops.map((workshop) => (
              <WorkshopCard key={workshop.id} workshop={workshop} />
            ))}
          </div>
        )}
      </main>
    </>
  );
}
